function Form(props) {
    const [data, setData, setStatus] = [props.data, props.setData, props.setStatus]
    const fetchData = e => {
        e.preventDefault();
        setData([...data, { text: e.target[0].value, completed: false, id: Math.random() * 1000 , date_time: new Date().toLocaleString()}])
        e.target[0].value = '';
    }
    const sortHandler = (e) => {
        setStatus(e.target.value)
    }
    return (
        <div className="form">
            <form onSubmit={fetchData}>
                <input type="text" required className="todo-input" />
                <button className="todo-button" type="submit">
                    <i className="fas fa-plus-square"></i>
                </button>
                <div class="select">
                    <select onChange={sortHandler} name="todos" className="filter-todo">
                        <option value="all">All</option>
                        <option value="completed">Completed</option>
                        <option value="incomplete">Not completed</option>
                    </select>
                </div>

            </form>
        </div>
    )
}
export default Form